//Login Main Container
.login {
  width: 27em;
  height: 40em;
  //min-height: 44em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  position: relative;
  z-index: -1;
  margin-top: 50px;

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
    border-radius: 4px;
    position: relative;
    z-index: 99;
    width: 100%;
    height: 100%;
    z-index: 99;
    padding: 17px 10px;
    //transition: transform 200ms ease-in-out;
  }

  .right-side {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 90%;
    background-color: #6CADDF;
    width: 100%;
    position: absolute;
    right: -34%;
    border-radius: 6px;
    z-index: 1;
    transition: all 400ms ease-in-out;
    cursor: pointer;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);

    &.right {
      right: -40%;
      align-items: flex-end;

      &:hover {
        right: -45%;
      }
    }

    &.left {
      right: 40%;
      align-items: flex-start;

      &:hover {
        right: 45%;
      }
    }

    .text {
      font-size: 21px;
      font-weight: 500;
      color: #fff;
      margin-right: 3em;
      margin-left: 3em;
    }
  }
}


@media (min-width:250px) and (max-width:780px) {
  .right-side {
    width: 3%;
    height: 1rem;
    align-items: center;
    top: 30%;
    left: -.2rem;
    margin-right: 5em;
  }
  .login {
    width: 27em;
    height: 40em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    position: relative;
    z-index: -1;
    margin-bottom: 200px;
    /* margin-top: 50px; */
}

  .inner-container {
    height: 40px;
 
    bottom: 100%;
    align-items: center;
    border-radius: 6px;
    margin-bottom: -30rem;
   
    cursor: pointer;
    


    &.right {
      bottom: 100rem;

      // top: -50rem;
      // right: -50%;
      // align-items: center;
      // margin-top: 10rem;
      &:hover {
        top: -50rem;
      }
    }

    &.left {
      bottom: 40%;
      align-items: center;

      &:hover {
        bottom: 45%;
      }
    }

    .text {
      font-size: 15px;
      font-weight: 500;
      color: #fff;
      // margin-right: 3em;
      // margin-left: 3em;
    }
  }

  .login .right-side .text {
    font-size: 21px;
    font-weight: 500;
    color: #fff;
    margin-right: 3em;
    margin-left: 3em;
    margin-bottom: -22em;
  }

  .login .right-side {
    width: 101%;
  }
}