/*Carousel*/
.carrouselt {
  background: #f5f5f5;
  text-align: center;
  padding: 4em 15px;
 width: 80vw;
  height: 7.5em;
  max-width: 750px;
  margin: 20vh auto;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
}
.carrouselt h2 {
  margin: 0;
  margin-top: -1.7em;
  padding: 0;
  font-size: 1em;
  text-align: center;
  color: #bbbbbb;
}
.carrouselt .slidest {
  width: 420%;
  left: 0;
  padding-left: 0;
  /* padding-top: 1em; */
  overflow: hidden;
  list-style: none;
  position: relative;
  transition: transform .5s;
}
.carrouselt .slidest li {
  width: 25%;
  position: relative;
  float: left;
}
.carrouselt li p{
  margin-top: 0;  
}
.carrouselt li q {
  max-width: 90%;
  margin: auto;
  color: #666666;
  font-size: 1.3em;
  font-weight: bold;
}
.carrouselt li img {
  width: 3em;
  /* height: 10vw; */
  object-fit: cover;
  border-radius: 50%;
  margin-left: -1.5em;
  margin-right: 0.5em;
  vertical-align: middle;
}
.carrouselt li span.author {
  margin-top:0.5em;
  font-size: 1.2em;
  color: #777777;
  display: block;
}
.carrouselt .slidesNavigationt {
  display: block;
  list-style: none;
  text-align: center;
  bottom: 1em;
  /*--- Centering trick---*/
    /* Absolute positioning*/
    position: absolute; 
    /* Abosulte positioning*/
    width: 104px; /*This width  is the addition of the width of all the navigations dots - So in this case is   104 because the navigation dots are 26px (width:10px and 6px marginleft + 6 px marginright) and there are 4 dots so 4x26=104 */
    left: 50%; /*Centering de element*/
    margin-left: -52px; /*adjusting the centering by applying a negative margin of half of the width*/
}
.carrouselt input {
  display: none;
}

.slick-dots{
  display: flex !important;
  flex-direction: row !important;
  justify-content: center !important;
}
.carrouselt .slidesNavigationt label {
  float: left;
  margin: 6px;
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: solid 2px #2980b9;
  font-size: 0;
}
/* You have to repeat this with each slide
TODO: make it easier with SCSS
25% movement 100/slides-num
*/
#radio-1t:checked ~ .slidest {
  transform: translateX(0%);
}
#radio-2t:checked ~ .slidest {
  transform: translateX(-25%);
}
#radio-3t:checked ~ .slidest {
  transform: translateX(-50%);
}
#radio-4t:checked ~ .slidest {
  transform: translateX(-75%);
}


.carrouselt .slidesNavigationt label:hover {
   cursor: pointer;
}
/* You have to repeat this with each slide/dot */
.carrouselt #radio-1t:checked ~ .slidesNavigationt label#dotForRadio-1t,
.carrouselt #radio-2t:checked ~ .slidesNavigationt label#dotForRadio-2t,
.carrouselt #radio-3t:checked ~ .slidesNavigationt label#dotForRadio-3t,
.carrouselt #radio-4t:checked ~ .slidesNavigationt label#dotForRadio-4t {
  background: #2980b9;
}

@media  (max-width: 796px) {
  .carrouselt{
    height: 8.5em;
  }
  .App {
    width: 100%;
    height: auto;
    height: initial;
    z-index: -3;
}
}
@media  (max-width: 480px) {
  .carrouselt li p {
    padding-left: 0.5em;
    padding-right: 0.5em;
  }
  .carrouselt li q {
      font-size: 1em;
  }
  .carrouselt li img {
     width:2em;
     margin-left: -1em;
     margin-right: 0.25em;
  }
}



